import React, {useEffect,useState} from 'react'
import { makeStyles } from '@material-ui/core/styles';
import {AppBar, IconButton, Toolbar, Collapse} from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import SortIcon from '@material-ui/icons/Sort';
import Particles from 'react-particles-js';
import { Link as  Scroll } from 'react-scroll'
import ListenOnScroll from '../hook/listenOnScroll';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '100vh',
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover',
    backgroundImage: `url(${process.env.PUBLIC_URL + '/Assets/background4.jpg'})`,

  },
  appbar: {
    background:"none",
  },
  icon: {
    color: '#1d3557',
    fontSize: '1rem',
  },
  appbarTitle: {
    flexGrow: '1',
    color: '#1d3557'
  },
  appbarWrapper: {
    width: '80%',
    margin: '0 auto'
  },
  colorText: {
    color: '#f1faee'
  },
  container: {
    textAlign: 'center'
  },
  GoDown: {
    color: '#e63946',
    fontSize: '3em'
  }

}));

export default function Header() {
	const classes = useStyles();
  const [checked,setChecked] = useState(false);
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  useEffect(()=>{
    setChecked(true);
  },[])
	return (

		<div className={classes.root} id='header'>

		  <AppBar className={classes.appbar} elevation={0}>
      <Toolbar className={classes.appbarWrapper}>
      <h1 className={classes.appbarTitle}> My Portfolio. </h1>
        <IconButton >
          <Button className={classes.icon} aria-controls="main_menu" aria-haspopup="true" onClick={handleClick}>
             Menu
           </Button>
           <Menu
             id="main_menu"
             anchorEl={anchorEl}
             keepMounted
             open={Boolean(anchorEl)}
             onClose={handleClose}
           >
            <Scroll to='about-me' smooth={true}>
             <MenuItem>About me</MenuItem>
             </Scroll>
             <Scroll to='work-experience' smooth={true}>
              <MenuItem>Work Experience</MenuItem>
              </Scroll>
              <Scroll to='projects' smooth={true}>
               <MenuItem>Projects</MenuItem>
               </Scroll>
               <Scroll to='certificates' smooth={true}>
                <MenuItem>Skills</MenuItem>
                </Scroll>
           </Menu>
        </IconButton>
        </Toolbar>
      </AppBar>
      <Collapse in={checked} {...(checked ? {timeout: 1000} : {})} CollapsedHeight={50}>

      <div className={classes.container}>
        <h1>
          Welcome to <br /> My <span className={classes.colorText}> Portfolio. </span>
        </h1>
        <Scroll to='about-me' smooth={true}>
        <IconButton>
          <ExpandMoreIcon className={classes.GoDown}/>

        </IconButton>
        </Scroll>
      </div>
      </Collapse>

		</div>
	)
}
