import React from 'react'
import { makeStyles } from '@material-ui/core/styles';

import ListenOnScroll from '../hook/listenOnScroll';
import Grid from '@material-ui/core/Grid';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import { CardMedia, Button } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  large: {
      width: theme.spacing(10),
      height: theme.spacing(10),

    },
    center: {
      justifyContent: 'center',
      display: 'flex',

    }

}));

export default function Badges() {
	const classes = useStyles();

	return (
		<div>
      <Grid container spacing={2}>
        <Grid item xs={12} className={classes.center}>
        <Typography variant="h6" gutterBottom className={classes.head} align='center'>
          Badges
        </Typography>
        </Grid>
        <Grid item xs={6} className={classes.center}>
          <Avatar alt="Remy Sharp" src="./Assets/badges/TFBadge.png" className={classes.large}/>
        </Grid>
        <Grid item xs={6} className={classes.center}>
          <Avatar alt="Remy Sharp" src="./Assets/badges/data-science-professional-certificate.png" className={classes.large}/>
        </Grid>
        <Grid item xs={12} className={classes.center}>
          <Button variant="contained" color="primary" href="https://www.youracclaim.com/users/hansley-kowlessur/badges">
            Check more on Acclaim
          </Button>
        </Grid>
        <Grid item xs={12} className={classes.center}>
          <Button variant="contained" color="primary" href="https://www.credential.net/9f6890b3-2faa-4205-b29a-dfc2de1a07c5#gs.p2w0rl">
            Tensorflow Certified Developer
          </Button>
        </Grid>


      </Grid>

		</div>
	)
}
