import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import ListenOnScroll from '../hook/listenOnScroll';

import { CarouselProvider, Slider, Slide, ButtonBack, ButtonNext, ImageWithZoom, Image, DotGroup} from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Badges from './Badges';
import Carousel from './Carousel';
import Development from './Development';
import LinkedInIcon from '@material-ui/icons/LinkedIn';

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100vh',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center'
  }
}));

export default function Certificates() {
	const classes = useStyles();


	return (
		<div className={classes.root} id='certificates'>
    <Container>
      <Grid container>
        <Grid item xs={12} spacing={5}>
          <Typography variant="h4" gutterBottom  align='center'>
            Skills
          </Typography>
        </Grid>
        <Grid item xs={4}>
            <Carousel />
        </Grid>
        <Grid item xs={4}>
            <Badges />
        </Grid>
        <Grid item xs={4}>
            <Development />
        </Grid>
        
       </Grid>

      </Container>
		</div>
	)
}
