import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Particles from 'react-particles-js';
import ImageCard from './ImageCard';
import abouts from '../static/about';
import ListenOnScroll from '../hook/listenOnScroll';
import Grid from '@material-ui/core/Grid';
import Avatar from '@material-ui/core/Avatar';
import Timeline from '@material-ui/lab/Timeline';
import TimelineItem from '@material-ui/lab/TimelineItem';
import TimelineSeparator from '@material-ui/lab/TimelineSeparator';
import TimelineConnector from '@material-ui/lab/TimelineConnector';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineDot from '@material-ui/lab/TimelineDot';
import TimelineOppositeContent from '@material-ui/lab/TimelineOppositeContent';
import Typography from '@material-ui/core/Typography';


const useStyles = makeStyles((theme) => ({
  root: {
    display: 'inline',
    justifyContent: 'center',
    alignItems: 'center'
  },
  content: {
    maxHeight: 200
  }

}));

export default function Development() {
	const classes = useStyles();


	return (
		<div className={classes.root}>
    <Typography variant="h5" align='center'>Experience</Typography>
      <Timeline className={classes.content}>
        <TimelineItem>
          <TimelineOppositeContent>
            <Typography variant="body2">Core Language</Typography>
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color='primary' />
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent>
            <Typography variant="caption">Java, Python, JavaScript, C#</Typography>
          </TimelineContent>
        </TimelineItem>

        <TimelineItem>
          <TimelineOppositeContent>
            <Typography variant="body2">Web Development</Typography>
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color='primary'/>
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent>
            <Typography variant="caption">HTML, CSS, Bootstrap, React, PHP, Ajax</Typography>
          </TimelineContent>
        </TimelineItem>

        <TimelineItem>
          <TimelineOppositeContent>
            <Typography variant="body2">Machine Learning, Image processing, NLP</Typography>
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color='primary' />
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent>
            <Typography variant="caption">Tensorflow, SpaCy, NLP libraries</Typography>
          </TimelineContent>
        </TimelineItem>


        <TimelineItem>
          <TimelineOppositeContent>
            <Typography variant="body2">Visualisation</Typography>
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color='primary'/>
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent>
            <Typography variant="caption">Kibana, Vega, Vega-Lite, Matplotlib</Typography>
          </TimelineContent>
        </TimelineItem>


        <TimelineItem>
          <TimelineOppositeContent>
            <Typography variant="body2">Database(SQL)</Typography>
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color='primary'/>
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent>
            <Typography variant="caption">SQL Server, MySQL, Oracle 11c</Typography>
          </TimelineContent>
        </TimelineItem>

        <TimelineItem>
          <TimelineOppositeContent>
            <Typography variant="body2">NoSQL</Typography>
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color='primary'/>
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent>
            <Typography variant="body2">ElasticSearch</Typography>
          </TimelineContent>
        </TimelineItem>

      </Timeline>

		</div>
	)
}
