import React from 'react'
import { makeStyles } from '@material-ui/core/styles';

import ListenOnScroll from '../hook/listenOnScroll';
import Grid from '@material-ui/core/Grid';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import Typography from '@material-ui/core/Typography';
import CardHeader from '@material-ui/core/CardHeader';

import { CardMedia, Button } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import Container from '@material-ui/core/Container';

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100vh',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center'
  },

}));

export default function Personal() {
	const classes = useStyles();

  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };

	return (
		<div className={classes.root} id='projects'>
    <Container>
      <Grid container   direction="row" alignItems="center">
        <Grid item xs={12}>
          <Typography variant="h5" gutterBottom className={classes.head} align='center'>
            Personal/Commercial projects
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <Slider {...settings} >
            <div>
                <Card>
                    <CardHeader
                      title="Assay Office Website"
                    />
                    <CardMedia
                      image='./Assets/AssayOffice.mp4'
                      component='video'
                      controls
                    />
                    <CardContent>
                      <Typography variant="body2" color="textSecondary" component="p">
                        Website of Assay office (HTML,Bootstrap, CSS, Ajax, JavaScript, MySQL, PHP)
                      </Typography>
                      <Button variant="contained" color="primary" href="#contained-buttons">
                        Link
                      </Button>
                    </CardContent>
                </Card>
            </div>
            <div>
            <Card>
                <CardHeader
                  title="Sentiment Analysis using Flask/React"
                />
                <CardMedia
                  image='./Assets/Sentiment.mp4'
                  component='video'
                  controls
                />
                <CardContent>
                  <Typography variant="body2" color="textSecondary" component="p">
                    API call is made to a flask server form React.
                  </Typography>
                  <Button variant="contained" color="primary" href="#contained-buttons">
                    Link
                  </Button>
                </CardContent>
            </Card>
            </div>
            <div>
            <Card>
                <CardHeader
                  title="Weather App"
                />
                <CardMedia
                  image='./Assets/WeatherApp.mp4'
                  component='video'
                  controls
                />
                <CardContent>
                  <Typography variant="body2" color="textSecondary" component="p">
                    API call is made to OpeaWeather API, and the contents are extracted.
                  </Typography>
                  <Button variant="contained" color="primary" href="#contained-buttons">
                    Link
                  </Button>
                </CardContent>
            </Card>
            </div>
            <div>
            <Card>
                <CardHeader
                  title="Image Gallery"
                />
                <CardMedia
                  image='./Assets/ImageGallery.mp4'
                  component='video'
                  controls
                />
                <CardContent>
                  <Typography variant="body2" color="textSecondary" component="p">
                    Display list of images using unsplash API.
                  </Typography>
                  <Button variant="contained" color="primary" href="#contained-buttons">
                    Link
                  </Button>
                </CardContent>
            </Card>
            </div>
          </Slider>
        </Grid>
      </Grid>
      </Container>
		</div>
	)
}
