import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import ListenOnScroll from '../hook/listenOnScroll';

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";


const useStyles = makeStyles((theme) => ({
  root: {
    justifyContent: 'center',
    alignItems: 'center'

  },

  head: {
    width: '100%'
  },

  img: {
    height: '100%',
    width: '100%'
  }

}));

export default function Carousel() {
	const classes = useStyles();


  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };

	return (
    <div className={classes.root} >
      <Grid container   direction="row" alignItems="center">
        <Grid item xs={12}>
          <Typography variant="h6" gutterBottom className={classes.head} align='center'>
            Certificates
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <Slider {...settings} >
            <div>
                <img src='./Assets/certificates/HarvardCS50.jpg' className={classes.img}/>
            </div>
            <div>
                <img src='./Assets/certificates/DataScienceMath.jpg' className={classes.img}/>
            </div>
            <div>
                <img src='./Assets/certificates/MachineLearning.jpg' className={classes.img}/>
            </div>
            <div>
                <img src='./Assets/certificates/TFCertificate.jpg' className={classes.img}/>
            </div>
          </Slider>
        </Grid>
      </Grid>
		</div>
	)
}
