import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Particles from 'react-particles-js';
import ImageCard from './ImageCard';
import abouts from '../static/about';
import ListenOnScroll from '../hook/listenOnScroll';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { Container } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100vh',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center'
  },

}));

export default function WorkExperience() {
	const classes = useStyles();
  const checked = ListenOnScroll('header');

  return (
		<div className={classes.root} id='work-experience'>
    <Container>
    <Grid container >
      <Grid item xs={12}>
      <Typography variant="h4" gutterBottom className={classes.head} align='center'>
        Work Experience
      </Typography>
      </Grid>
      <Grid item xs={12} spacing={4}>
        <Grid container>
          <Grid item xs={4}>
              <ImageCard about={abouts[0]} checked= {checked}/>
          </Grid>
          <Grid item xs={4}>
            <ImageCard about={abouts[1]} checked= {checked}/>
          </Grid>

          <Grid item xs={4}>
              <ImageCard about={abouts[2]} checked= {checked}/>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
    </Container>
		</div>
	)
}
